/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/
/* All Pages - Common Design */
.site-content {

    display: flex;
    flex-flow: row wrap;
    width: auto;

    padding-top: 50px;

    .home &,
    .page-about &,
    .single-adventure & {
        padding-top: 0;
    }


    .content-area {
        width: 100%;
    }


    body {

        display: flex;
        flex-flow: column;
    }

    .hfeed {
        height: 100vh;
    }

    a {
        color: $color__link;

        &:visited {
            color: $color__link-visited;
        }

        &:hover,
        &:focus,
        &:active {
            color: $color__link-hover;
        }

        &:focus {
            outline: thin dotted;
        }

        &:hover,
        &:active {
            outline: 0;
        }
    }

    /* Front Page */

    .front-page-banner {
        min-height: 100vh;
        background: url('/Inhabitent/wp-content/themes/inhabitent/images/home-hero.jpg') no-repeat;
        background-size: cover;


        display: flex;
        justify-content: center;
        align-items: center;


    }

    /* Shop Category */
    .product-info {
        margin-top: 3rem;
        padding-bottom: 3rem;

        h2 {
            text-align: center;
            margin: 1.5rem auto;
        }

        .front-page-shop-category-container {
            @include center-block;
            width: 960px;
            text-align: center;

            display: flex;
            justify-content: space-between;

            .front-page-shop-category {
                padding: 1rem;
                border: 1px solid #e1e1e1;
                width: 20%;
                min-width: 13rem;
                box-shadow: 3px 3px 5px #fff9c0, 0 0 0.75em #248A83;


                img {
                    height: 3.5rem;
                    width: 3.5rem;
                }

                a {
                    text-decoration: none;
                    color: #fff;

                    border: 1px solid #248A83;
                    border-radius: 0 1rem;
                    padding: 0.75rem;
                    background: #248A83;

                    &:hover {
                        a {
                            color: #fff;
                            background: $color__button-hover;
                        }
                    }

                }
            }
        }
    }

    /* Journal Category */
    .journal-info-container {

        display: flex;
        flex-flow: column wrap;
        align-items: center;

        padding-bottom: 3rem;

        h2 {
            text-align: center;
            margin: 1.5rem auto;
        }

        .front-page-journal-container {

            display: flex;
            flex-flow: row wrap;
            width: 960px;

            .front-page-post-container {
                @include center-block;
                flex: 0 1 30%;
                text-align: center;

                display: flex;
                flex-flow: column wrap;

                min-width: 13rem;
                border: 1px solid #e1e1e1;

                .front-page-post-thumbnail {

                    display: flex;
                    width: 100%;
                    height: 50%;
                }

                .front-page-post-wrapper {

                    display: flex;
                    flex-flow: row wrap;
                    justify-content: flex-start;
                    padding-left: 1rem;
                    margin-top: 1rem;

                    .front-page-post-date {
                        padding-right: 0.5rem;
                    }

                    .front-page-post-comment {
                        padding-left: 0.5rem;
                    }
                }

                .front-page-post-title {

                    padding-left: 1rem;
                    line-height: 1.25;
                    font-size: 0.9rem;
                    text-align: left;

                    a {
                        text-decoration: none;
                    }
                }

                .front-page-post-permalink {

                    a {
                        text-decoration: none;
                        color: $color__border-abbr;

                        border: 1px solid $color__border-abbr;
                        border-radius: 0 1rem;
                        padding: 0.5rem;
                        background: #fff;
                        display: inline-block;
                    }

                    &:hover {
                        a {
                            color: #fff;
                            background: $color__link;
                        }
                    }
                }
            }
        }
    }

    /* Adventures Category */

    .adventures-info-container {

        h2 {
            text-align: center;
            margin: 1.5rem auto;
        }

        .adventures-info-list {

            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            grid-template-rows: 1fr 1fr;
            grid-column-gap: 15px;
            grid-row-gap: 15px;

            .story-container-full {

                grid-area: 1 / 1 / 3 / 3;

                img {
                    max-height: 600px;
                    max-width: none;
                    position: relative;
                }

                .story-info {
                    position: absolute;

                    a {
                        // position: absolute;
                    }
                }
            }

            .story-container-long {

                grid-area: 1 / 3 / 2 / 5;

                img {
                    max-height: 600px;
                    max-width: none;
                    position: relative;
                }

                .story-info {
                    position: absolute;

                    a {}
                }
            }

            .story-container-short:first-child {
                grid-area: 2 / 3 / 3 / 4;

                img {
                    max-height: 600px;
                    max-width: none;
                    position: relative;
                }

                .story-info {
                    position: absolute;

                    a {}
                }
            }

            .story-container-short:last-child {
                grid-area: 2 / 4 / 3 / 5;

                img {
                    max-height: 600px;
                    max-width: none;
                    position: relative;
                }

                .story-info {
                    position: absolute;

                    a {}
                }
            }

        }

    }



    /* About Page */



    .page-about-section {
        .entry-content> :not(header) {
            margin-left: auto;
            margin-right: auto;
            max-width: 800px;
        }

        .entry-content {
            margin-top: 0;
            margin-bottom: $base__space-unit + 0.5rem;

            header {
                margin-bottom: $base__space-unit * 2;
            }


            h2 {
                text-align: center;
                margin: 0 auto;
            }

            p {
                &:last-of-type {
                    margin-bottom: 0;
                    margin: 0 auto;
                    padding: $base__space-unit;
                }
            }
        }
    }




    .hero-image-header {
        height: 100vh;
        font-family: Novecento Sans Web, sans-serif;
        text-transform: uppercase;

        h1 {
            color: #fff;
            font-family: Novecento Sans Web, sans-serif;
            align-self: center;
            text-shadow: 1px 1px 2px #d99054, 0 0 1em #248A83, 0 0 0.2em #248A83;
        }

    }

    .page-about-content {

        padding: 3rem;
    }


    /* Shop Page - Archive */

    /* Product Type Page - Archive */
    .archive-product-taxonomies {
        margin: 0;
        padding: 0;

        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        align-items: center;

        .archive-product-taxonomies-list {
            font-family: $font__alt;
            list-style-type: none;

            margin: 0 $base__space-unit - 1;

            a {
                text-decoration: none;
            }
        }
    }

    .archive-product-item {

        a {
            color: $color__link;

            &:visited {
                color: $color__link-visited;
            }

            &:hover,
            &:focus,
            &:active {
                color: $color__link-hover;
            }

            &:focus {
                outline: thin dotted;
            }

            &:hover,
            &:active {
                outline: 0;
            }
        }

        .archive-product-container {

            .archive-product-image {
                background-color: #e1e1e1;
                height: 250px;
                overflow: hidden;
                position: relative;

                img {
                    left: 50%;
                    max-height: 250px;
                    max-width: none;
                    position: absolute;
                    top: 50%;
                    transform: translate(-50%, -52%);
                    width: auto;
                }
            }

            .archive-product-meta {

                padding: ($base__space-unit / 2) round($base__space-unit * 0.667);

                display: flex;
                flex-flow: row wrap;
                justify-content: space-between;
                align-items: center;

                .archive-product-title {
                    .entry-title {
                        font-size: 1rem;
                        font-weight: 400;
                        font-family: Merriweather Web, serif;
                        margin: 0;
                        padding: 0;
                        text-transform: capitalize;

                        a {
                            color: #404040;
                            text-decoration: none;
                        }

                        &::before {

                            width: 0;
                            white-space: nowrap;

                        }

                    }

                }

                .archive-product-price {
                    font-size: 1rem;
                }
            }
        }
    }

    /* Single Product Page */

    .single-product .hentry {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;

        .single-product-image {
            width: 50%;
        }

        .single-product-title {
            color: $color__text-footer-h3;
        }

        .single-product-price {
            margin-top: 0;
            text-transform: uppercase;
        }

        .single-product-content {
            margin-top: 0;
        }


    }

